.featured {
    height: 900px;
}
.featured--vertical {
    width: inherit;
    height: inherit;
    background: linear-gradient(to top, #111 20%, transparent 60%);
}
.featured--horizontal{
    width: inherit;
    height: inherit;
    background: linear-gradient(to right, #111 10%, transparent 90%);
    display: flex;
    flex-direction: column;
    justify-content: center;
    padding-left: 56px;
    padding-bottom: 150px;
    padding-top: 70px;
}
.featured--name{
    font-size: 60px;
    font-weight: bold;

}
.featured--info{
    font-size: 20px;
    font-weight: bold;
    margin-top:15px;
}

.featured--points, 
.featured--year, 
.featured--seasons{
    display: inline-block;
    margin-right: 15px;
}

.featured--points{
    color: #46d369;
}

.featured--description{
    margin-top: 15px;
    font-size: 20px;
    color: #fff;
    max-width: 40%;
}

.featured--buttons{
    margin-top: 15px;
}

.featured--watchbutton,
.featured--mylistbutton{
    display: inline-block;
    font-size: 17px;
    font-weight: bold;
 
    border-radius: 5px;
    text-decoration: none;
    margin-right: 10px;
    opacity: 1;
    transition: all ease 0.2s;
}

.featured--watchbutton:hover,
.featured--mylistbutton:hover{
    opacity: 0.7;
}

.featured--watchbutton{
    background-color: #fff;
    color: #000;
    padding: 10px 30px;
}

.featured--mylistbutton{
    background-color: rgba(51, 51, 51, 0.4);
    color: #fff;
    padding: 10px 60px;
}

.featured--genres{
    margin-top: 15px;
    font-size: 18px;
    color: #999;
}

@media (max-width:760px){
    .featured{
        height: 700px;
    }
    .featured--name{
        font-size: 40px;
    }
    .featured--info{
        font-size: 16px;
    }
    .featured--description{
        font-size: 14px;
        max-width: 100%;
        margin-right: 30px;
    }
    .featured--watchbutton,
    .featured--mylistbutton{
        font-size: 16px;
    }
    .featured--genres{
        font-size: 14px;
    }
}